.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

h4 {
  color: red;
}

.footer-menu {
  height: 20vw;
  width: 100%;
  margin-top: 20px;
  display: flex;
  background-color: #0c162b;

  ul {
    margin-top: 5px;
    display: grid;
    justify-content: space-between;

    li {
      margin-top: -50px;
      list-style-type: none;


      :hover {
        font-size: 14.8px;
        transition: 0.3s all ease-in-out;
        text-decoration: underline;
      }

      a {
        font-size: 15px;
        color: #fff;
      }
    }
  }

}